import React, { useState, useCallback, useRef } from "react";
import {
  HStack,
  VStack,
  Center,
  Text,
  Pressable,
  useToast,
  Hidden,
  useBreakpointValue,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import EmailIcon from "../../components/svg/EmailIcon";
import PasswordIcon from "../../components/svg/PasswordIcon";
import EyeClosedIcon from "../../components/svg/EyeClosedIcon";
import EyeOpenIcon from "../../components/svg/EyeOpenIcon";
import UserIcon from "../../components/svg/UserIcon";
import CompanyIcon from "../../components/svg/CompanyIcon";
import * as authActions from "../../store/actions/auth";
import { vw, vh } from "../../util";
import WhiteLogo from "../../components/svg/WhiteLogo";
import Colors from "../../constants/Colors";

const ShadowButton = React.lazy(() => import("../../components/ShadowButton"));
const LogoBlock = React.lazy(() => import("../../components/LogoBlock"));
const TermsBlock = React.lazy(() => import("../../components/TermsBlock"));
const WelcomeBlock = React.lazy(() => import("../../components/WelcomeBlock"));
const ShadowAuthInput = React.lazy(() =>
  import("../../components/ShadowAuthInput")
);

const Register = ({ page, setPage }) => {
  const sz = useBreakpointValue({
    base: "base",
    sm: "sm",
    lg: "lg",
    md: "md",
    xl: "xl",
  });
  const fRef = useRef([]);
  const toast = useToast();
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [isInviteCode, setIsInviteCode] = useState(false);
  const [inviteMessage, setInviteMessage] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "all",
  });

  const _showAlert = useCallback(
    (status, msg) => {
      toast.show({
        duration: 2000,
        placement: "top",
        render: () => {
          let bg = "red.300";
          if (status === "error") {
            bg = "red.300";
          } else if (status === "success") {
            bg = "green.300";
          } else if (status === "info") {
            bg = "blueGray.300";
          } else if (status === "warning") {
            bg = "orange.300";
          }
          return (
            <Text
              style={{
                fontSize: vw(12),
                fontFamily: "Poppins_medium",
                color: "#33214B",
              }}
              p={2}
              bg={bg}
              rounded={"full"}
            >
              {msg}
            </Text>
          );
        },
      });
    },
    [toast]
  );

  const onSubmit = async (data) => {
    try {
      setIsLoading(true);
      const postData = {
        firstname: data.firstname,
        lastname: data.lastname,
        company: data.company,
        email: data.email,
        password: data.password,
      };

      let searchParams = new URLSearchParams(
        window.location.search.replace("?", "")
      );
      if (searchParams.get("aff_click_id")) {
        postData.aff_click_id = searchParams.get("aff_click_id");
      }
      if (searchParams.get("aff_sub")) {
        postData.aff_sub = searchParams.get("aff_sub");
      }
      if (data.invitecode && data.invitecode.trim() !== "") {
        postData["invitecode"] = data.invitecode.trim();
      }

      await authActions.signup(postData);
      const action = authActions.login(data.email, data.password);
      await dispatch(action);
      _showAlert("success", "Registration successful.");
      if (postData.aff_click_id) {
        await fetch(
          `http://adtrustmedia.go2cloud.org/aff_lsr?offer_id=1&transaction_id=${postData.aff_click_id}`
        );
      }
      setIsLoading(false);
      setPage("login");
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  return (
    <HStack h={"100vh"} justifyContent={"space-between"}>
      <Hidden only={["base", "sm"]}>
        <LogoBlock />
      </Hidden>
      <Center style={{ width: vw(766) }} flex={1} justifyContent={"center"}>
        <VStack
          style={{
            marginTop: vh(36),
          }}
          flex={1}
        >
          <Hidden from={"md"}>
            <Center style={{ marginTop: vh(-20), marginBottom: vh(5) }}>
              <WhiteLogo width={vw(24)} fill={Colors.primary} />
            </Center>
          </Hidden>
          <WelcomeBlock />
          <ShadowAuthInput
            ref={(el) => (fRef.current["firstname"] = el)}
            onKeyPress={(e) => {
              if (e.key === "Tab") {
                e.preventDefault();
                fRef.current["lastname"].focus();
              }
            }}
            name='firstname'
            label={"First Name"}
            placeholder='First Name'
            control={control}
            rules={{
              required: true,
              message: "Please enter first name",
            }}
            error={errors.firstname ? true : false}
            style={{ marginTop: vh(sz === "sm" || sz === "base" ? -18 : 8) }}
            inputStyle={{}}
            leftIcon={
              <UserIcon
                style={{
                  marginRight: vw(10),
                }}
                width={vw(24)}
                height={vh(24)}
                fill={errors.firstname ? "red" : "#000"}
              />
            }
          />
          <ShadowAuthInput
            ref={(el) => (fRef.current["lastname"] = el)}
            onKeyPress={(e) => {
              if (e.key === "Tab") {
                e.preventDefault();
                fRef.current["company"].focus();
              }
            }}
            name='lastname'
            label={"Last Name"}
            placeholder='Last Name'
            control={control}
            rules={{
              required: true,
              message: "Please enter last name",
            }}
            error={errors.lastname ? true : false}
            inputStyle={{}}
            leftIcon={
              <UserIcon
                style={{
                  marginRight: vw(10),
                }}
                width={vw(24)}
                height={vh(24)}
                fill={errors.lastname ? "red" : "#000"}
              />
            }
          />
          <ShadowAuthInput
            ref={(el) => (fRef.current["company"] = el)}
            onKeyPress={(e) => {
              if (e.key === "Tab") {
                e.preventDefault();
                console.log("Tab");
                fRef.current["email"].focus();
              }
            }}
            name='company'
            label={"Company"}
            placeholder='Company'
            control={control}
            rules={
              {
                // required: true,
                // message: "Please enter your email address",
                // pattern: {
                //   value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                //   message: "invalid email address",
                // },
              }
            }
            error={errors.company ? true : false}
            inputStyle={{}}
            leftIcon={
              <CompanyIcon
                style={{
                  marginRight: vw(10),
                }}
                width={vw(24)}
                height={vh(24)}
                fill={errors.company ? "red" : "#000"}
              />
            }
          />
          <ShadowAuthInput
            ref={(el) => (fRef.current["email"] = el)}
            onKeyPress={(e) => {
              if (e.key === "Tab") {
                e.preventDefault();
                console.log("Tab");
                fRef.current["password"].focus();
              }
            }}
            name='email'
            label={"Email"}
            placeholder='Email'
            autoComplete='new-password'
            control={control}
            rules={{
              required: true,
              message: "Please enter your email address",
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "invalid email address",
              },
            }}
            error={errors.email ? true : false}
            inputStyle={{}}
            leftIcon={
              <EmailIcon
                style={{
                  marginRight: vw(10),
                }}
                width={vw(24)}
                height={vh(24)}
                fill={errors.email ? "red" : "#000"}
              />
            }
          />
          <ShadowAuthInput
            ref={(el) => (fRef.current["password"] = el)}
            onKeyPress={(e) => {
              if (e.key === "Tab") {
                e.preventDefault();
                console.log("Tab");
                fRef.current["invitecode"].focus();
              }
            }}
            type={showPassword ? "text" : "password"}
            name='password'
            label={"Password"}
            placeholder='Password'
            autoComplete='new-password'
            control={control}
            rules={{
              required: {
                value: true,
                message: "Please enter your password",
              },
            }}
            error={errors.password ? true : false}
            inputStyle={{}}
            leftIcon={
              <PasswordIcon
                style={{
                  marginRight: vw(10),
                }}
                width={vw(24)}
                height={vh(24)}
                fill={errors.password ? "red" : "#000"}
              />
            }
            rightIcon={
              <Pressable
                onPress={() => {
                  setShowPassword(!showPassword);
                }}
              >
                {!showPassword ? (
                  <EyeOpenIcon
                    style={{
                      marginRight: vw(4),
                    }}
                  />
                ) : (
                  <EyeClosedIcon
                    style={{
                      marginLeft: vw(10),
                    }}
                    width={vw(24)}
                    height={vh(24)}
                    //   fill={errors.password ? "red" : "none"}
                  />
                )}
              </Pressable>
            }
          />
          <ShadowAuthInput
            ref={(el) => (fRef.current["invitecode"] = el)}
            onKeyPress={(e) => {
              if (e.key === "Tab") {
                e.preventDefault();
                setIsLoading(true);
                handleSubmit(onSubmit, () => {
                  setIsLoading(false);
                })();
              } else if (e.key === "Enter") {
                e.preventDefault();
                setIsLoading(true);
                handleSubmit(onSubmit, () => {
                  setIsLoading(false);
                })();
              }
            }}
            name='invitecode'
            label={"Invite Code"}
            placeholder='Invite code (Optional)'
            control={control}
            // rules={{
            //   required: true,
            //   message: "Please enter first name",
            // }}
            error={errors.invitecode ? true : false}
            style={{ marginTop: vh(8) }}
            inputStyle={{}}
            leftIcon={
              <PasswordIcon
                style={{
                  marginRight: vw(10),
                }}
                width={vw(24)}
                height={vh(24)}
                fill={errors.invitecode ? "red" : "#000"}
              />
            }
          />
          <Center marginTop={vh(42)}>
            <ShadowButton
              label={"Let's get started"}
              isLoading={isLoading}
              onPress={handleSubmit(onSubmit)}
              style={{
                width: vw(320),
                height: vh(57),
              }}
            />
          </Center>
          <Center style={{ marginTop: vh(29) }}>
            <HStack space={2} justifyContent={"space-between"}>
              <Text
                _web={{
                  color: "rgba(48, 48, 48, 1);",
                  fontSize: vw(14),
                  fontFamily: "Poppins_medium",
                  textAlign: "center",
                }}
              >
                Have an account?
              </Text>
              <Pressable
                onPress={() => {
                  setPage("login");
                }}
                _web={{
                  //   height: vh(40),
                  alignSelf: "center",
                  borderBottomWidth: vw(2),
                  borderBottomColor: "rgba(125, 43, 233, 1)",
                }}
              >
                <Text
                  _web={{
                    height: vh(21),
                    color: "rgba(125, 43, 233, 1)",
                    fontSize: vw(14),
                    fontFamily: "Poppins_medium",
                    textAlign: "center",
                  }}
                >
                  Sign in here
                </Text>
              </Pressable>
            </HStack>
          </Center>
          <TermsBlock />
        </VStack>
      </Center>
    </HStack>
  );
};

export default Register;
